/* tab css */
.tab {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  max-width: 860px;
  align-self: center;
  height: 100%;
}
.tab>div:first-child {
  display: flex;
  flex: 1 1 auto;
}
.tab>div:last-child {
  display: flex;
  align-items: center;
}
.tab-sm>div:last-child {
  height: 2.5rem;
}
.tab-md>div:last-child {
  height: 3.2rem;
}
.tab-lg>div:last-child {
  height: 4rem;
}
.tab>div:last-child, .tab>div:last-child>div {
  display: flex;
  align-items: center;
}
.tab>div:last-child>div {
  flex-direction: column;
}
.tab>div>div {
  overflow-x: hidden;
  overflow-y: auto;
  width: 100%;
}
.tab>div>div>div {
  position: relative;
}
.tab>div>div>div>u {
  position: absolute;
  right: -0.8rem;
  top: 0rem;
  font-size: 0.7rem;
  background-color: red;
  display: flex;
  align-items: center;
  color: white;
  min-width: 1rem;
  padding: 0 3px;
  height: 1.0rem;
  text-decoration: none;
  justify-content: center;
  border-radius: 0.6rem;
}
.tab>div>div>div>u.dot {
  width: 0.5rem;
  height: 0.5rem;
  min-width: 0.5rem;
  right: -0.8rem;
}

/* form css */
.border-gray {
  border-color: #cccccc !important;
}
.form-row-checkbox {
  min-width: 1.8rem;
  max-width: 1.8rem;
  padding: 0 0 0 0.2rem;
  text-align: left;
  z-index: 999;
}
.form-row-checkbox input {
  height: 1.1rem;
  width: 1.1rem;
  min-width: 1.1rem;
  cursor: pointer;
}
.form-row-edit {
  min-width: 1.8rem;
  max-width: 1.8rem;
  padding: 0 0.2rem 0.2rem 0.2rem;
  text-align: right;
  cursor: pointer;
  align-self: end;
  z-index: 999;
}
.form-row-content {
  flex: 1 1;
  width: calc(100% - 3.6rem)
}
.form-row-content-1 {
  flex: 1 1;
  width: calc(100% - 1.8rem)
}
.cursor-pointer {
  cursor: pointer;
}
.row-selected label {
  font-weight: bold;
}
.required-item {
  border-color: #007bff;
}
.deleted, .deleted label, .deleted input {
  -webkit-text-decoration: line-through rgb(255, 127, 127);
          text-decoration: line-through rgb(255, 127, 127);
  color: gray;
